'use client';
import { useToast } from "@/hooks/use-toast";
import { useRouter } from "next/navigation";
import {
    createContext,
    useContext,
    useState,
    useEffect,
    ReactNode,
} from 'react';

type UserRole = 'student' | 'host' | 'admin' | null;

interface AuthContextType {
    isAuthenticated: boolean;
    userRole: UserRole;
    login: (role: Exclude<UserRole, null>) => void;
    logout: () => void;
    isLoading: boolean;
}

const AuthContext = createContext<AuthContextType | null>(null);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
    const router = useRouter();
    const { toast } = useToast();
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [userRole, setUserRole] = useState<UserRole>(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        setUserRole('admin');
        const verifyToken = async () => {
            setIsLoading(true);
            try {
                const response = await fetch('/api/verify-token', {
                    credentials: 'include',
                });

                if (response.ok) {
                    const data = await response.json();
                    setIsAuthenticated(true);
                    setUserRole(data.role as UserRole);
                } else {
                    // If the token is invalid, we guarantee a full logout
                    await fetch('/api/logout',
                        {
                            credentials: 'include',
                        }
                    );
                    toast({
                        title: "Logout Successful",
                        description: "You have been logged out successfully",
                        duration: 3000,
                        className: "bg-green-600 text-white border-0",
                    });
                    // التوجيه حسب دور المستخدم
                    setTimeout(() => {
                        router.push("/");
                    }, 500);
                    setIsAuthenticated(false);
                    setUserRole(null);
                }
            } catch (error) {
                toast({
                    variant: "destructive",
                    title: error,
                    description: "Token verification failed",
                });
            } finally {
                setIsLoading(false);
            }
        };

        // verifyToken();
    }, []);

    const login = (role: Exclude<UserRole, null>) => {
        setIsAuthenticated(true);
        setUserRole(role);
    };

    const logout = async () => {
        try {
            // أولاً: نرسل طلب تسجيل الخروج إلى الخادم
            await fetch('/api/logout', { credentials: 'include' });

            // ثانياً: نحدث حالة التطبيق
            setIsAuthenticated(false);
            setUserRole(null);
        } catch (error) {
            console.error('Logout failed', error);
        }
    };

    return (
        <AuthContext.Provider value={{
            isAuthenticated,
            userRole,
            login,
            logout,
            isLoading
        }}>
            {children}
        </AuthContext.Provider>
    );
};

export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
};